.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 100vh;
  font-family: Arial, sans-serif;
  background-color: rgb(243 243 243 / 90%);
}

.chat-box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-height: 80vh;
  width: 90%;
  margin-bottom: 20px;
  overflow-y: auto;
  padding: 10px;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);

  /*width: 80%;
  max-width: 600px;
  height: 70vh;
  overflow-y: auto;
  border: 1px solid #333333;
  border-radius: 8px;
  padding: 16px;
  margin-bottom: 20px;
  background-color: white;*/
}

.message {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 10px;
  width: 100%;
}

.user-message {
  justify-content: flex-end;

  /*text-align: left;
  margin: 10px 10px 10px 40px;
  padding: 10px;
  background-color: #dcf8c6;
  border-radius: 8px;
  width: auto;
  max-width: 295px;*/
}

.bot-message {
  justify-content: flex-start;

  /*text-align: left;
  margin: 10px 40px 10px 10px;
  padding: 10px;
  background-color: #eaeaea;
  border-radius: 8px;*/
}

.message-bubble {
  max-width: 75%;
  padding: 10px 15px;
  border-radius: 20px;
  background-color: #e1ffc7;
  color: #333;
  font-size: 16px;
  box-shadow: 0 2px 3px rgba(0, 0, 0, .1);
}

.user-message .message-bubble {
  background-color: #d0f0fd;
}

.bot-message .message-bubble {
  background-color: #e1ffc7;
}

.message-bubble::before {
  content: '';
  position: absolute;
  width: 0;
  height: 0;

}

.user-message .message-bubble::before {
  right: -10px;
  top: 50%;
  border-top: 10px solid #d0f0fd;
  transform: translateY(-50%);
}

.bot-message .message-bubble::before {
  right: -10px;
  top: 50%;
  border-top: 10px solid #d0f0fd;
  transform: translateY(-50%);
}

.loading-message {
  text-align: center;
  font-size: 18px;
  color: #888;
}

.input-area {
  display: flex;
  align-items: center;
  padding: 10px;
  border-radius: 25px;
  width: 80%;
  max-width: 600px;
  background-color: #f0f0f0;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.input-field {
  flex-grow: 1;
  padding: 12px 15px;
  border: none;
  border-radius: 20px;
  font-size: 16px;
  background-color: #fff;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .1);
  outline: none;
}

.input-field::placeholder{
  color: #888;
}

.input-field:focus {
  border: 2px solid #007bff;
  box-shadow: 0 0  5px rgba(0, 123, 255, .2);
}

.send-button {
  padding: 10px;
  margin-left: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.3s;
}

.send-button:hover {
  background-color: #0056b3;
}

.send-button:focus {
  outline: none;
}
